.steps {
  margin: 0 16px 6px;
}
@media (min-width: 724px) {
  .steps {
    padding: 16px;
    margin: 0;
  }
  .steps_columns {
    display: grid;
    grid-template-columns: 328fr 360fr;
    gap: 48px;
  }
}

.cards {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
@media (min-width: 724px) {
  .cards {
    display: grid;
    grid-template-columns: 344px 344px;
  }
  .cards_single {
    display: flex;
  }
}
.cards__step-four {
  gap: 16px;
}

.step {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin: 32px 0 16px;
}
.step__tabs {
  margin: 8px 0;
}
.step__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
}
@media (min-width: 724px) {
  .step__title_hide-tablet {
    display: none;
  }
}
@media (min-width: 724px) {
  .step {
    margin: 0;
  }
}

.card-content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.card-content__label {
  font-size: 14px;
  line-height: 20px;
  color: rgb(92, 92, 92);
}
.card-content__text_bold {
  font-weight: 600;
}
.card-content__text span {
  margin-left: 4px;
}

.bg-tabs {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  gap: 4px;
  padding: 0 10px;
}
.bg-tabs__button {
  position: relative;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border: 1px solid transparent;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.2s;
}
.bg-tabs__button.active {
  border-color: rgb(1, 144, 219);
}
.bg-tabs__button img {
  width: 40px;
  height: 40px;
}

.button-wrapper {
  width: 100%;
  display: flex;
  padding: 16px 0;
}
.button-wrapper__button {
  width: 100%;
}
@media (min-width: 724px) {
  .button-wrapper__button {
    width: auto;
    min-width: 160px;
  }
}

.header-first {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 16px 8px;
  background-color: rgb(255, 255, 255);
}
.header-first__not-mobile {
  display: none;
}
@media (min-width: 724px) {
  .header-first {
    display: flex;
    margin-top: 32px;
  }
}
.header-first__title {
  margin: 0 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
}
@media (min-width: 724px) {
  .header-first__title {
    font-size: 36px;
    line-height: 48px;
  }
}
.header-first__text {
  margin: 0 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: rgb(92, 92, 92);
}
@media (min-width: 724px) {
  .header-first__text {
    font-size: 16px;
    line-height: 24px;
  }
}

.header-second {
  position: sticky;
  z-index: 100;
  top: 0;
  display: flex;
  align-items: center;
  min-height: 72px;
  padding: 12px 8px;
  background-color: rgb(255, 255, 255);
}
.header-second__title {
  margin: 0 8px;
  font-weight: 600;
}
@media (min-width: 724px) {
  .header-second__title {
    font-size: 20px;
    line-height: 24px;
  }
}
.header-second__back-button {
  display: flex;
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0 8px 0 0;
  border: none;
  background-color: transparent;
  color: rgb(92, 92, 92);
  cursor: pointer;
}
.header-second__back-icon {
  width: 32px;
  height: 32px;
}
.header-second__steps {
  display: flex;
  flex-shrink: 0;
  flex-wrap: nowrap;
  gap: 4px;
  margin-left: auto;
  margin-right: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: rgb(92, 92, 92);
}
@media (min-width: 724px) {
  .header-second__steps {
    font-size: 16px;
    line-height: 24px;
  }
}
.header-second.selected .header-second__back-button {
  display: flex;
}
.header-second.selected .header-second__steps {
  display: flex;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 192px;
  padding: 16px 24px;
  border-radius: 24px;
  border: 1px solid rgba(0, 0, 0, 0.04);
  transition: all 0.2s;
}
.card.interactive {
  height: auto;
  cursor: pointer;
}
@media (min-width: 724px) {
  .card.interactive {
    min-height: 106px;
  }
}
.card:nth-child(7n+1) {
  background-color: rgb(244, 240, 225);
}
.card:nth-child(7n+2) {
  background-color: rgb(230, 241, 228);
}
.card:nth-child(7n+3) {
  background-color: rgb(231, 235, 247);
}
.card:nth-child(7n+4) {
  background-color: rgb(241, 228, 233);
}
.card:nth-child(7n+5) {
  background-color: rgb(241, 235, 228);
}
.card:nth-child(7n+6) {
  background-color: rgb(228, 240, 241);
}
.card:nth-child(7n) {
  background-color: rgb(238, 228, 241);
}
.card.interactive:hover:nth-child(7n+1) {
  background-color: rgb(233, 225, 195);
}
.card.interactive:hover:nth-child(7n+2) {
  background-color: rgb(205, 227, 201);
}
.card.interactive:hover:nth-child(7n+3) {
  background-color: rgb(194, 204, 235);
}
.card.interactive:hover:nth-child(7n+4) {
  background-color: rgb(227, 201, 211);
}
.card.interactive:hover:nth-child(7n+5) {
  background-color: rgb(227, 215, 201);
}
.card.interactive:hover:nth-child(7n+6) {
  background-color: rgb(201, 225, 227);
}
.card.interactive:hover:nth-child(7n) {
  background-color: rgb(221, 201, 227);
}
.card__name {
  display: flex;
  align-items: flex-start;
  gap: 4px;
  z-index: 10;
}
.card__name-hit {
  display: none;
  z-index: 10;
}
.card.interactive .card__name-hit {
  display: flex;
  align-items: center;
}
.card__name-hit-icon {
  width: 24px;
  height: 24px;
  color: rgb(255, 59, 56);
  z-index: 10;
}
.card__name-hit-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: rgb(219, 40, 53);
  z-index: 10;
}
.card__status {
  display: none;
  margin-top: 2px;
  font-size: 12px;
  line-height: 16px;
  color: rgb(92, 92, 92);
  z-index: 10;
}
.card.interactive .card__status {
  display: block;
}
.card.interactive .card__address {
  display: none;
}
.card.interactive .card__description {
  display: none;
}
.card.interactive .card__price {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.card__address {
  margin-top: 2px;
  margin-bottom: auto;
  font-size: 12px;
  line-height: 16px;
  color: rgb(92, 92, 92);
  z-index: 10;
}
.card__description {
  margin-top: 8px;
  font-size: 14px;
  line-height: 20px;
  z-index: 10;
}
.card__price {
  display: flex;
  flex-wrap: nowrap;
  gap: 2px;
  margin-top: 8px;
  font-size: 28px;
  line-height: 42px;
  z-index: 10;
}
.card__bg {
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: -1px;
  right: -1px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 24px;
  z-index: 1;
  opacity: 0;
  transition: all 0.2s;
}
.card__bg.active {
  opacity: 1;
}
.card__bg_1 {
  background-image: url("../img/gradient01.png");
}
.card__bg_2 {
  background-image: url("../img/gradient02.png");
}
.card__bg_3 {
  background-image: url("../img/gradient03.png");
}
.card__bg_4 {
  background-image: url("../img/gradient04.png");
}
.card__bg_5 {
  background-image: url("../img/gradient05.png");
}
.card__img {
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: -1px;
  right: -1px;
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: contain;
  z-index: 2;
  border-radius: 24px;
  opacity: 0;
  transition: all 0.2s;
}
.card__img.active {
  opacity: 1;
}
.card__img_1 {
  background-image: url("../img/baloon.png");
}
.card__img_2 {
  background-image: url("../img/cake.png");
}
.card__img_3 {
  background-image: url("../img/gift.png");
}
.card__img_4 {
  background-image: url("../img/heart.png");
}
.card__img_5 {
  background-image: url("../img/flower.png");
}
.card__buttons {
  z-index: 30;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
.card__button {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 8px;
  background-color: rgb(255, 255, 255);
  cursor: pointer;
  color: rgb(92, 92, 92);
  transition: all 0.2s;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.04), 0px 2px 6px 0px rgba(0, 0, 0, 0.04), 0px 0px 1px 0px rgba(0, 0, 0, 0.04);
}
.card__button:hover {
  color: rgb(31, 31, 31);
}
.card__button:active {
  color: rgb(1, 144, 219);
}
.card__button-icon {
  width: 24px;
  height: 24px;
}
.card__button_left {
  margin-left: -12px;
}
.card__button_right {
  margin-right: -12px;
}
.card__button_right .card__button-icon {
  transform: rotate(180deg);
}

.tabs {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 4px;
  border-radius: 8px;
  background-color: rgba(1, 144, 219, 0.12);
}
.tabs__button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
  margin: 0;
  padding: 0 8px;
  border-radius: 8px;
  border: 1px solid transparent;
  background-color: transparent;
  color: rgb(92, 92, 92);
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
  transition: all 0.2s;
}
.tabs__button:hover {
  color: rgb(1, 144, 219);
}
.tabs__button.active {
  border-color: rgba(1, 144, 219, 0.32);
  background-color: rgb(255, 255, 255);
  color: rgb(0, 111, 188);
}

.checkbox-wrapper {
  display: flex;
  flex-direction: column;
}

.checkbox {
  display: flex;
  align-items: center;
}
.checkbox input[type=checkbox] {
  display: none;
}
.checkbox input[type=checkbox]:checked + .checkbox__label-wrapper label {
  color: rgb(31, 31, 31);
}
.checkbox input[type=checkbox]:checked + .checkbox__label-wrapper label::before {
  border-color: rgb(1, 144, 219);
  background-color: rgb(1, 144, 219);
}
.checkbox input[type=checkbox]:checked + .checkbox__label-wrapper label .checkbox__icon-ok {
  opacity: 1;
}
.checkbox input[type=checkbox].error + .checkbox__label-wrapper label::before {
  border-color: rgb(255, 59, 56);
}
.checkbox__label-wrapper {
  position: relative;
  padding-left: 36px;
}
.checkbox__label {
  color: rgb(92, 92, 92);
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
  user-select: none;
  transition: color 0.2s;
}
.checkbox__label:hover {
  color: rgb(31, 31, 31);
}
.checkbox__label:hover::before {
  border-color: rgb(1, 144, 219);
}
.checkbox__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 24px;
  height: 24px;
  border-radius: 8px;
  border: 2px solid rgb(143, 143, 143);
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;
  transition: all 0.2s;
}
.checkbox__link {
  color: rgb(0, 111, 188);
  text-decoration: none;
}
.checkbox__icon-ok {
  position: absolute;
  left: 5px;
  top: 5px;
  width: 13px;
  height: 13px;
  fill: rgb(255, 255, 255);
  opacity: 0;
  transition: opacity 0.2s;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border-width: 1px;
  border-style: solid;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button__icon {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  fill: #5c5c5c;
}
.button.s {
  padding: 7px 23px;
}
.button.l {
  padding: 11px 31px;
}
.button.m {
  padding: 15px 31px;
  font-size: 16px;
}
.button.primary {
  border-color: rgb(87, 187, 68);
  background-color: rgb(87, 187, 68);
  color: rgb(255, 255, 255);
}
.button.primary:hover:not(:disabled) {
  border-color: rgb(70, 150, 54);
  background-color: rgb(70, 150, 54);
}
.button.primary:active:not(:disabled) {
  border-color: rgb(70, 150, 54);
  background-color: rgb(70, 150, 54);
}
.button.primary:disabled {
  border-color: rgb(87, 187, 68);
  background-color: rgb(87, 187, 68);
  opacity: 0.48;
}
.button.secondary {
  background-color: transparent;
  border-color: rgba(1, 144, 219, 0.48);
  color: #1f1f1f;
}
.button.secondary:hover:not(:disabled) {
  color: #0190db;
}
.button.secondary:active:not(:disabled) {
  border-color: #00539d;
  color: #006fbc;
}
.button.secondary:disabled {
  border-color: rgba(1, 144, 219, 0.48);
  opacity: 0.48;
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__label {
  margin-bottom: 8px;
  color: rgb(31, 31, 31);
  font-size: 14px;
  line-height: 20px;
}
.text-input__input {
  padding: 10px 40px 10px 16px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  color: rgb(31, 31, 31);
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgb(143, 143, 143);
}
.text-input__input:hover {
  border-color: rgba(1, 144, 219, 0.48);
}
.text-input__input:focus {
  outline: none;
  border-color: rgba(1, 144, 219, 0.48);
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-input__input.error {
  border-color: rgba(255, 59, 56, 0.48);
}
.text-input__input_force-alert {
  border-color: rgba(255, 59, 56, 0.48);
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 16px;
  width: 16px;
  height: 16px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset.active {
  display: flex;
}
.text-input__icon {
  width: 16px;
  height: 16px;
  fill: rgb(143, 143, 143);
  transition: all 0.2s;
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  color: #ff3b38;
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__sub-text {
  margin-top: 4px;
  color: rgb(92, 92, 92);
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  color: #ff3b38;
  font-size: 14px;
  line-height: 20px;
}

.link-button {
  display: flex;
  padding: 10px 0;
}
.link-button__button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: rgb(0, 111, 188);
  transition: all 0.2s;
}
.link-button__button:hover {
  color: rgb(0, 83, 157);
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  min-height: 100%;
  font-family: "SF Pro Display", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: rgb(31, 31, 31);
  background-color: rgb(255, 255, 255);
}

.main {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 768px;
  margin: 0 auto;
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}